dden;border-radius:4px}.card:hover{-webkit-transform:scale(1.25);transform:scale(1.25);z-index:2}.card img{object-fit:cover;height:80px;width:160px}.card .data-container{position:absolute;top:0;opacity:0;width:100%;height:100%;transition:0.2s}.card .data-container:hover{opacity:1}.card .data-container:hover ul{-webkit-transform:scaleY(1);transform:scaleY(1)}.card .data-container ul{color:#f7f7f7;padding-top:5px;display:grid;height:100%;width:100%;text-align:center;transition:0.3s;-webkit-transform:scaleY(0.3);transform:scaleY(0.3);z-index:20}.card .data-container ul:after{content:"";width:100%;height:100%;position:absolute;top:0;left:0;transition:0.02s}.card .data-container ul:hover::after{background:rgba(0,0,0,0.65)}.card .data-container li{font-size:0.8rem;z-index:30}.card .data-container li:nth-child(1){font-weight:bold;color:#61dafb}

